.btn__normal {
  font-family: inherit;
  font-size: var(--fs-400);
  border-radius: var(--default-border-radius);
  cursor: pointer;
  transition: font-weight var(--transition-speed-slow) ease;
  background: none;
  border: none;
  outline: none;
  padding: .8em 2em;
  font-weight: 400;
  position: relative;
}

.btn__normal:before {
  content: "";
  border-radius: var(--default-border-radius);
  width: 100%;
  height: 100%;
  z-index: -999;
  opacity: 0;
  transition: opacity var(--transition-speed-slow) ease, transform var(--transition-speed-slow) ease;
  background-color: #0003;
  display: block;
  position: absolute;
  inset: 0;
  transform: translate(0);
}

.btn__normal:hover:before {
  opacity: 1;
  transform: translate(5px, 5px);
}

.btn__normal:hover {
  font-weight: 500;
}

.btn__normal--hollow {
  border: 1px solid var(--clr-btn);
  background-color: #fff;
}

.btn__transparent {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

.btn__normal:focus-visible, .btn__transparent:focus-visible {
