  transition: opacity var(--transition-speed-normal) ease var(--transition-delay-normal), transform var(--transition-speed-normal) ease var(--transition-delay-normal), padding var(--transition-speed-normal) ease var(--transition-delay-fast), right var(--transition-speed-normal) ease var(--transition-delay-fast);
